export default {
  async asyncData({ $strapi, route }) {
    const id = route.params.products
    debugger;
    const currentProduct = await $strapi.$productos.findOne(id)
    debugger;
    return { currentProduct }
  },
  data() {
    return {
      currentProduct: {},
    }
  },
}
